.about_container{
    display:grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}
.about_me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent,var(--color-primary),transparent);
    display: grid;
    place-items: center;
}
.about_me-image{
    border-radius: 2rem;
    overflow: hidden;
    /* transform: rotate(10deg); */
    transition: var(--transition) ;
}
.about_me-image:hover{
    transform: rotate(0);
}

.about_cards{
    display: grid;
    grid-template-columns:repeat(2,1fr) ;
    gap: 1.5rem;
}
.about_card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about_card:hover{
    background: transparent;
    background-color: var(--color-primary-variant);
    cursor: default;
}

.about_icon{
    color: white;
    font-size: 1.4rem;
    margin-bottom: 1rem;

}
.about_card h5{
    font-size: 0.95rem;
}

.about_card small{
    font-size: 0.7rem;
    color: var(--color-light);
}
.about_content p{
    margin: 2rem 0 2.6rem;
    color:var(--color-light);
}
/* for tab=============== */
@media screen and (max-width:1024px){
   .about_container{
    grid-template-columns: 1fr;
    gap: 0;
   }
   .about_me{
    width: 50%;
    margin: 2rem auto 4rem;
   }
   .about_content p{
    margin: 1rem 0 1.5rem;
   }
  }
  /* for mobile======== */
  @media screen and (max-width:600px){
    .about_me{
        width: 65%;
        margin: 0 auto 3rem;
       }
       .about_cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
       }
       .about_content{
        text-align: center;
       }
       .about_content p{
        margin: 1.5rem 0;
       }
  }
  .Container{
    display: flex;
    gap: 2rem;
    padding-top: 3rem;
    width: 80%;
    max-width: 1280px;
    margin: 0 auto;
    z-index: 1;
}
    @media screen and (max-width: 840px) {
        .Container{
            width: 90% 
        }
}
@media screen and (max-width: 640px) {
    .Container{
        flex-direction: column;
    }
}
.Texts{
    flex:1
}
.Texts h1{
    font-size: 2rem;
    font-family: "Secular One", sans-serif;
    letter-spacing: 2px
}
.Texts h3{
    font-weight: 500;
    font-size: 1.2rem;
    padding-bottom: 1.2rem;
    text-transform: capitalize;
}
.Texts p{
    font-weight: 300;
}
.Texts button{
    padding: 0.7rem 2rem;
    margin-top: 3rem;
    cursor: pointer;
    background-color: #01be96;
    border: none;
    color: #fff;
    font-weight: 500;
    filter: drop-shadow(0px 10px 10px #01be9551);
}
.Texts button:hover{
    filter: drop-shadow(0px 10px 10px #01be9570);
}
.Profile{
    width: 25rem;
}
.Profile img{
    width: 100%;
    filter: drop-shadow(0px 10px 10px #01be9570);
    transition: transform 400ms ease-in-out;
}
@media screen and (max-width: 800px) {
    .Profile {
        width: 20rem
    }
  } 

  @media screen and (max-width: 660px) {
    .Profile {

        width: 18rem
    }
  }

  @media screen and (max-width: 640px) {
      .Profile {
        width: 100%

    }
  } 

.Profile:hover img{
    transform: translateY(-10px);
}