.outerFooter{
    background: var(--color-primary );
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
.outerFooter a{
    color: var(--color-bg);
}
.footer_logo{
    font-size: 4rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block; 
}
.footer_logo img{
    width: 100px;
    border-radius: 50%;
    height: 100px;
    border: 2px solid var(--color-bg);
}
.permalinks{
    display: flex;
    flex-wrap:wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}
.footer_socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}
.footer_socials a{
    background: var(--color-bg);
    color:var(--color-white);
    padding:0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
    font-size: 100px;
}
.footer_socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}
.footer_copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);

}
.footer-icon{
    font-size: 2rem;
    margin: 1rem;
}
 /* for mobile======== */
 @media screen and (max-width:600px){
   .permalinks{
    flex-direction: column;
    gap: 1.5rem;
   }
   .footer_socials{
    margin-bottom: 2.6rem;
   }
  }
