nav{
    background: rgba(0,0,0,0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
    
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;

}

nav a:hover{
    background: rgba(0,0,0,0.3);
    color: aqua;
}
/* nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
} */
.toolTip{
    color: var(--color-primary);
}

/*Styles for screens smaller than 768px (mobile) */
@media (max-width: 767px) {
  nav {
    padding: 0.5rem 1rem;
    bottom: 1rem;
  }

  nav a {
    padding: 0.5rem;
    font-size: 0.9rem;
  }
}

/* Styles for screens larger than 768px (tablets and up) */
@media (min-width: 768px) {
  nav {
    padding: 1rem 2rem;
    bottom: 2rem;
  }

  nav a {
    padding: 0.8rem;
    font-size: 1.1rem;
  }
}

/* Styles for screens larger than 1024px (laptops and up) */
@media (min-width: 1024px) {
  nav {
    padding: 1rem 2rem;
    bottom: 3rem;
  }

  nav a {
    padding: 1rem;
    font-size: 1.3rem;
  }
}