.project-container{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    font-family: sans-serif;
    gap: 1.5rem;
  }
  #project1{
    background-image: url("project1.png");
  }
  #project2{
    background-image: url("project2.png");
  }
  #project3{
    background-image: url("project3.png");
  }
  #project4{
    background-image: url("https://user-images.githubusercontent.com/115461550/229465563-d6f7f749-3070-4214-887d-f38f9bf949ac.png");
  }
  .project-card {
    color: var(--clr-neutral-100);
    background-size: cover;
    padding: 10rem 0 0;
    width: 100%;
    border-radius: 2rem;
    overflow: hidden;
    transition: transform 200ms ease;
    /* height: ; */
  }
  
  .project-card:hover,
  .project-card:focus-within {
    transform: scale(1.05);
  }
  
  .card-content {
    --padding: 1rem;
    padding: var(--padding);
    background: #4a5568;
  }
  
  .project-title {
    position: relative;
    width: max-content;
  }
  
  .project-card:hover .project-title::after,
  .project-card:focus-within .project-title::after {
    transform: scaleX(1);
  }
  
  .project-title::after {
    content: "";
    position: absolute;
    height: 4px;
    width: calc(100% + var(--padding));
    left: calc(var(--padding) * -1);
    bottom: -2px;
    background: var(--clr-accent-400);
    transform-origin: left;
    transition: transform 500ms ease;
  }
  
  @media (hover) {
    .card-content {
      transform: translateY(65%);
      transition: transform 500ms ease;
    }
  
    .card-content > *:not(.project-title) {
      opacity: 0;
      transition: opacity 500ms linear;
    }
  
    .project-card:hover .card-content > *:not(.project-title),
    .project-card:focus-within .card-content > *:not(.project-title) {
      opacity: 1;
      transition-delay: 700ms;
    }
  
    .project-card:hover .card-content,
    .project-card:focus-within .card-content {
      transform: translateY(0);
      transition-delay: 500ms;
    }
  
    .project-card:focus-within .card-content {
      transition-duration: 0ms;
    }
  
    .project-title::after {
      transform: scaleX(0);
    }
  }
  
  .button {
    cursor: pointer;
    display: inline-block;
    text-decoration: none;
    color: var(--clr-neutral-900);
    background-color: var(--clr-accent-400);
    padding: 0.5em 1.25em;
    border-radius: 0.25rem;
  }
  
  .button:hover,
  .button:focus {
    background-color: var(--clr-neutral-100);
    color:black
  }
  
  .project-description {
    color: rgb(255, 255, 255/0.85);
  }
  .portfolio_item-cta{
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
}
